.home {
    
    display: flex;
    flex-direction: column;
}
.home .index{
    background: url(./bg-jeep.jpg) no-repeat;
    min-height: 96vh;
    background-size:cover;
    background-position: center;
    /* display: flex;
    flex-direction: column; */
}
 .content-wrapper {
    flex: 1 0 auto;
    padding-top: 0px;

}
.content-bg {
    background: rgb(255 255 255 / 0.8);
    padding: 30px;
    min-height: 300px;
    max-height: calc(100vh - 100px);
    display: flex;
    overflow: auto;
    flex: 1 0 auto;
}
.content-inside {
    padding:60px 0px 0px 0px;
    max-height: calc(100vh - 100px);
   
}
@media screen and (max-width:1250px){
    .home .content-wrapper {
     padding-top: 60px;
    }
    .content-inside {
        padding:0px;
    }
}    
@media screen and (max-width:930px){
    .home .content-wrapper {
     padding-right: 60px;
     padding-left: 60px;
    }
}
@media screen and (max-width:650px){
    .home .content-wrapper {
        padding: 60px 60px 60px 60px;
    }
    .content-bg {
        padding: 40px;
    }
    .content-inside {
        padding:0px;
    }
}


